/* Keep your existing styles */
* {
  background-color:rgba(252,
    173,
    3,1);
}


.itemlist {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 80px;
  max-width: 1200px;
  max-height: 600px;
  margin: 0 auto;
}


.item {
  padding: 5px;
  border: 1px solid;
  background-color: rgb(240,
    86,
    14);
  text-align: center;
  border-radius: 25px;
  transition: transform 0.3s ease;
  border-color: yellow;
  box-shadow: 0 10px 9px rgba(0, 0, 0, 0.35);
}
.item:hover{
  transform:scale(1.2);

}


.item h1,
.item h2 {
  color: rgb(217, 239, 18);
  background-color: rgb(240,
    86,
    14);
  /* Match the background color of .item */
  padding: 5px;
  /* Add some padding to visually separate text from background */
  border-radius: 15px;
  /* Round the corners of the background */
}

/* Keep your existing styles */
.imgg {
  width: 150px;
  height: 150px;
}

.ok {
  background-color: rgb(240,
    86,
    14);
}
.but
{
  font-weight: 400;
  text-align: center;
  box-shadow: 2px black;
  font-size: medium;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-emphasis-color: red;
  width:100px;
  height: 50px;
  border-radius: 25px;
  background-color: orange;
  margin-left: 10px;
  margin-right: 10px;

}